body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  background: #d1defa;
  font-family: sans-serif, Arial, Helvetica;
  text-align: center;
  padding: 0px;
  margin: 0px;
}

.layout{
  width: 100%;
  height: auto;

  display: grid;
  grid-template-areas: 
  "header header header header"
  "nav nav nav nav"
  "content content content lateral"
  "footer footer footer footer";
  grid-template-rows: 100px 70px auto 70px;
}

/*Estilos del header*/

.header{
  grid-area: header;

  background: white;
  text-align: left;
  font-style: italic;

  display: flex;
  justify-content: center;
  align-items: center;
}

.logo{
  background: linear-gradient(90deg, #03228f 10%, #0e73e4 100%);
  width: 40px;
  height: 40px;
  border-radius: 300px;
}

.play{
  width: 0;
  height: 0;
  margin-top: 15px;
  margin-left: 16px;
  border-left: 12px solid white;
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
}

.header h1{
  margin-left: 15px;
}

/*Estilos de la barra de navegaciÃ³n*/

.nav{
  grid-area: nav;
  
  background: linear-gradient(90deg, #03228f 10%, #0e73e4 100%);
}

.nav > ul{
  display: flex;
  justify-content: center;
  align-items: center;
  list-style-type: none;
}

.nav li{
  padding: 10px;
}

.nav a{
  display: block;
  margin-left: 30px;
  margin-right: 30px;
  font-size: 18px;
  font-weight: lighter;
  color: white;
  text-decoration: none;
  transition: all 300ms;
}

.nav a:hover{
  transform: scale(1.2);
}

/*Estilos para el contenido principal*/

.content{
  grid-area: content;

  background: #f6f7f9;
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  align-items: flex-start;
}

.peli-item{
  flex-grow: 1;
  background: white;
  border-radius: 10px;
  box-shadow: 0px 0px 6px #ddd;
  margin: 10px;
  padding: 60px 17px;
}

button,
input[type="submit"],
input[type="button"]{
  width: 100px;
  background: linear-gradient(160deg, #03228f 0%, #0b70b1 75%);
  color: white;
  border: 0;
  outline: 0;
  padding: 15px;
  border-radius: 3px;
  margin-top: 15px;
  text-transform: capitalize;
  font-size: 16px;
  font-weight: 500px;
  cursor: pointer;
  transition: all 300ms;
}

.edit{
  margin-right: 7px;
}

.edit:hover{
  background: #e77b00;
}

.delete:hover{
  background: #9b0202;
}

/*Estilos barra lateral*/

.lateral{
  grid-area: lateral;

  background: #f6f7f9;
  border-left: 1px solid #ddd;
  padding: 20px;
}

.lateral div{
  box-sizing: border-box;
  width: 90%;
  margin: 20px;
  padding: 20px;
  padding-top: 10px;
  background: white;
  border-radius: 10px;
  box-shadow: 0px 0px 6px #ddd;
}

input[type="text"],
input[type="search"],
textarea{
  display: block;
  margin: 0 auto;
  margin-top: 15px;
  padding: 12px;
  border-radius: 5px;
  border: 1px solid #ddd;
  background: #f6f7f9;
  color: #454545;
  width: 80%;
  opacity: 4;
  outline: 0;
}

/*Estilos del footer*/

.footer{
  grid-area: footer;

  background: #d1defa;
  color: #454545;
  border-top: 1px solid white;
  text-align: left;
  line-height: 68px;
  padding-left: 25px;
}

.notFound{
  color: #9b0202;
}

@media (max-widht: 1085px){
  .content{
      flex-direction: column;
      justify-content: center;
      align-items: center;
  }

  .peli-item{
      width: 85%;
  }
}
